@import url(https://use.typekit.net/jjd1rvd.css);
body {
  margin: 0;
}
:root {
  --backgroundColor: #E1E5EA;
  --bordersColor: #A7BBC7;
  --inputColor: #E1EAF0;
  --fontColor: #4D4D4D;
}

body {
  font-family: objektiv-mk2;
  background-color: #E1E5EA;
  background-color: var(--backgroundColor);
}

.container {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

form {
  margin-top: 20px;
  width: 54%;
}

textarea {
  background-color: #E1EAF0;
  background-color: var(--inputColor);
  border: 3px solid #A7BBC7;
  border: 3px solid var(--bordersColor);
  border-radius: 5px;
  padding: 10px 14px;
}

textarea::-webkit-input-placeholder {
  font-family: sans-serif;
  font-size: 13px;
}

textarea:-ms-input-placeholder {
  font-family: sans-serif;
  font-size: 13px;
}

textarea::placeholder {
  font-family: sans-serif;
  font-size: 13px;
}

.check-box {
  display: flex;
  height: auto;
  width: 70%;
  text-align: center;
  margin-top: 20px;
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
}

.check-box input {
  margin-top: 20px;
}

.checkbox-message {
  color: red;
  position: relative;
  text-align: center;
}

.checkbox-warning {
  outline: 3px solid red;
}

.form-submit-btn {
  background-color: #5AA897;
  border-radius: 5px;
  padding: 10px 30px;
  border: 3px solid #A7BBC7;
  border: 3px solid var(--bordersColor);
  color: #4D4D4D;
  color: var(--fontColor);
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  margin: 50px 0;
  cursor: pointer;
  font-size: 20px;
  transition: .25s ease;
}

.form-submit-btn:hover {
  border: 3px solid #4D4D4D;
  border: 3px solid var(--fontColor);
}

footer {
  width: 100%;
  height: 40px;
  background-color: #A7BBC7;
  background-color: var(--bordersColor);
  display: flex;
  justify-content: center;
  align-items: center;
}

footer a {
  text-decoration: none;
  color: #4D4D4D;
  color: var(--fontColor);
}

@media screen and (max-width: 600px) {
  .check-box {
    width: 100%;
  }

  .check-box p {
    font-size: 7px;
  }

  .check-box input {
    margin-top: 10px;
  }

  form {
    margin-top: 20px;
    width: 90%;
  }
}
.header-box {
    width: 50%;
    height: 230px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 3px solid var(--bordersColor);
}

.header-box h3 {
    color: var(--fontColor);
    font-size: 40px;
    margin-bottom: -20px;
    padding-top: 20px;
    font-weight: 400;
}

.header-box h4 {
    color: var(--fontColor);
    font-size: 20px;
    margin-bottom: 20px;
    font-weight: 400;
}

.header-box img {
    width: 200px;
}

@media screen and (max-width: 600px) {
    .header-box {
        width: 80%;
        height: 260px;
    }

    .header-box h4 {
        text-align: center;
    }
}
.form-box {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
}

label {
    padding: 10px 0 5px 5px;
    color: var(--fontColor);
}

input {
    background-color: var(--inputColor);
    border: 3px solid var(--bordersColor);
    border-radius: 5px;
    padding: 10px 14px;
}
